<template>
  <el-dialog
    :title="title"
    :visible="value"
    :show-close="true"
    width="420px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <div>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="合同编号" prop="contractId">
          <el-select v-model="form.contractId" placeholder="请选择合同" style="width: 100%" @change="handleContract">
            <el-option
              v-for="item in contractList"
              :key="item.id"
              :label="item.code"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="订单编号" prop="orderId">
          <el-select v-model="form.orderId" placeholder="请选择订单" style="width: 100%" class="filter-item">
            <el-option
              v-for="item in orderList"
              :key="item.id"
              :label="item.code"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog('form')">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { getContractPullApi, getOrderPullApi, submitDeliveryToOrderApi } from '@/apis/modules/in-storage/purchase/delivery'

const INIT_FORM = {
  contractId: '',
  orderId: ''
}

export default {
  name: 'OrderSelect',
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '选择合同及订单'
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      form: INIT_FORM,
      loading: false,
      rules: {
        contractId: [
          { required: true, message: '请选择合同编号', trigger: 'blur' }
        ],
        orderId: [
          { required: true, message: '请选择订单编号', trigger: 'blur' }
        ]
      },
      contractList: [],
      orderList: []
    }
  },
  methods: {
    confirmDialog(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = false
          submitDeliveryToOrderApi({
            orderId: this.form.orderId,
            ...this.extraInfo
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    init() {
      const params = {}
      return new Promise((resolve, reject) => {
        getContractPullApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.contractList = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleContract(contractId) {
      return new Promise((resolve, reject) => {
        getOrderPullApi({
          contractId
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.orderList = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initSelect() {
      this.init()
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
