<!-- Tip 通过显示交接单按钮跳转的页面 -->
<template>
  <div class="app-container">
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="300"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <!-- 0: '草稿',
          1: '待检验',
          2: '检验中',
          3: '未入库',
          4: '部分入库',
          5: '已完成',
          6: '待更新' -->
          <el-button type="info" @click="handleDetail(scope.row)">详情</el-button>
          <el-button type="primary" :disabled="![0, 6].includes(scope.row.status)" @click="handleSubmit(scope.row)">提交</el-button>
          <!-- <el-button type="primary" :disabled="scope.row.status !== 0" @click="toContract(scope.row)">合同订单</el-button> -->
          <el-button type="warning" :disabled="![0, 6].includes(scope.row.status)" @click="handleModify(scope.row)">编辑</el-button>
          <el-button type="danger" :disabled="scope.row.status !== 0" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 合同和订单绑定  -->
    <model-select
      v-model="selectInfo.visible"
      :extra-info="{id: selectInfo.radio}"
      @submit="init"
    />

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import ModelSelect from './ContractDialog'
import { getListApi, deleteListApi, submitListApi } from '@/apis/modules/in-storage/purchase/delivery'
import { statusChartMap, statusMap } from '@/views/in-storage/purchase/status'

export default {
  components: {
    ModelSelect
  },
  mixins: [paginationPage],
  data() {
    return {
      title: '交接单',
      loading: false,
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'no',
          label: '交接单号',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'contractNo',
          label: '合同编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNo',
          label: '订单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'supplierName',
          label: '供应商',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialsUnit',
          label: '用料单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveryDate',
          label: '交货日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          minWidth: '86',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            return statusChartMap[status]
          },
          statusFormatter: (row) => {
            return statusMap[row.status]
          }
        }
      ],
      columnSets: [],
      supplierPullList: [],
      dialogType: 'add',
      selectInfo: {
        list: [],
        visible: false,
        radio: ''
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { orderId: this.$route.params.id } : {
        orderId: this.$route.params.id,
        ...this.query,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { pageData: { list, ...pageInfo }} = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 删除
    handleDelete(item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteListApi({
          entity: {
            id: item.id
          }
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    // 详情
    handleDetail(item) {
      // Tip 参数定义(view: 页面类型;orderId: 订单id;id: 交接单id;type: 订单类型)
      this.$router.push({
        name: 'InStorageConsignDeliveryDetail',
        params: {
          view: 'detail',
          id: item.id,
          orderId: this.$route.params.id,
          type: this.$route.params.type, // 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
          routeName: this.$route.name
        },
        query: {
          extraInfo: {
            ...this.$route.params
          }
        }
      })
    },
    // 提交
    handleSubmit(item) {
      this.$confirm('此操作将提交选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        submitListApi({
          deliveryReceiptId: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '提交成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消提交' })
      })
    },
    // 编辑
    handleModify(item) {
      // Tip 参数定义(view: 页面类型;orderId: 订单id;id: 交接单id;type: 订单类型)
      this.$router.push({
        name: 'InStorageConsignDeliveryModify',
        params: {
          view: 'modify',
          id: item.id,
          orderId: this.$route.params.id,
          type: this.$route.params.type, // 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
          routeName: this.$route.name
        },
        query: {
          extraInfo: {
            ...this.$route.params
          }
        }
      })
    },
    // 合同订单
    toContract(item) {
      this.selectInfo.radio = item.id
      this.selectInfo.visible = true
    }
  }
}
</script>

<style lang="scss" scoped>
</style>

