import request from '@/apis/request.wms'

// 主列表
export function getListApi(data = {}) {
  return request({
    url: '/enterWarehouse/wmsDeliveryReceipt/pageList',
    method: 'post',
    data
  })
}
// 主列表新增
export function addListApi(data = {}) {
  return request({
    url: '/enterWarehouse/wmsDeliveryReceipt/save',
    method: 'post',
    data,
    timeout: 10 * 60 * 1000 // 单位(ms)
  })
}

